export default {
	data() {
		return {
			passType:'password',
			passIcon: 'cuIcon-attention',
			formData: {
				username: '',
				password: ''
			}
		};
	},
	methods: {
		changeIcon() {
			console.log(123)
			this.passType === 'password' ? this.passType = 'text' : this.passType = 'password'
			this.passIcon === 'cuIcon-attention' ? this.passIcon = 'cuIcon-attentionforbid' : this.passIcon = 'cuIcon-attention'
		},
		loginTo() {
			if(!this.formData.username){
				uni.showModal({
				    title: '错误',
				    content: '请输入账号',
					showCancel: false,
				    success: function (res) {
				        if (res.confirm) {
				            console.log('用户点击确定');
				        } else if (res.cancel) {
				            console.log('用户点击取消');
				        }
				    }
				});
				return
			}else if (!this.formData.password) {
				uni.showModal({
				    title: '错误',
				    content: '请输入密码',
					showCancel: false,
				    success: function (res) {
				        if (res.confirm) {
				            console.log('用户点击确定');
				        } else if (res.cancel) {
				            console.log('用户点击取消');
				        }
				    }
				})
				return
			}
			uni.reLaunch({
			    url: "../index/index"
			});
			console.log(123)
		}
	}
};
